.rocket-image img {
  width: 150px;
  height: 150px;
  object-fit: fill;
}

.rocket-image {
  margin-right: 10px;
}

.rocket-item {
  display: flex;
  margin: 5px 0;
}

.rocket-item button {
  background-color: var(--accent-color);
  font-size: 0.8rem;
  color: white;
}

.rocket-item .description {
  font-size: 0.8rem;
}

.rocket-item .name {
  margin: 0;
  font-weight: bold;
}

button.reserved {
  background-color: white;
  color: grey;
  border: 1.5px solid grey;
}

.reserved-badge {
  padding: 0 0.2rem;
  background-color: #18a2b8;
  color: white;
  margin: 0 10px 0 0;
  border-radius: 5px;
}
